import { Component, OnInit } from "@angular/core";
import { SectionService } from "../services/section.service";
import { Router, ActivatedRoute } from "@angular/router";

import swal from "sweetalert2";

@Component({
  selector: "app-add-section",
  templateUrl: "./add-section.component.html",
  styleUrls: ["./add-section.component.scss"]
})
export class AddSectionComponent implements OnInit {
  newsection;
  submitting: boolean;

  constructor(
    public sectionervice: SectionService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.newsection = {};
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newsection);

    swal
      .fire({
        title: "Do you want to add the section ?",
        text: "New section will be added in Section List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.sectionervice.addSection(this.newsection).subscribe(
            (data: any) => {
              swal.fire("Add!", "Section has been added.", "success");
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
