import { Component, OnInit } from "@angular/core";

import { SectionService } from "../services/section.service";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";

@Component({
  selector: "app-edit-section",
  templateUrl: "./edit-section.component.html",
  styleUrls: ["./edit-section.component.scss"]
})
export class EditSectionComponent implements OnInit {
  updatedSection;
  sectionid;

  constructor(
    public sectionervice: SectionService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedSection = {};

    this.sectionid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.sectionervice.getsectionById(this.sectionid).subscribe(
      (data: any) => {
        console.log("sectionlist>>", data);
        this.updatedSection = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  update() {
    console.log("Submitted>>>>>>>>>", this.updatedSection);

    swal
      .fire({
        title: "Do you want to edit the section details ?",
        text: "You won't be able to revert the change",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Confirm"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.sectionervice.editsection(this.updatedSection).subscribe(
            (data: any) => {
              swal.fire("Edit!", "Section has been edited.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
