import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { SectionService } from "./services/section.service";
import swal from "sweetalert2";

@Component({
  selector: "app-section",
  templateUrl: "./section.component.html",
  styleUrls: ["./section.component.scss"]
})
export class SectionComponent implements OnInit {
  sectionList;

  constructor(public sectionservice: SectionService, private router: Router) {
    this.sectionList = [];
  }

  ngOnInit() {
    this.sectionservice.getSection().subscribe(
      (data: any) => {
        console.log("sectionlist>>", data);
        this.sectionList = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  edit(id) {
    console.log("section id>>", id);
    this.router.navigate(["section/edit/" + id]);
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the section ?",
        text: "You won't be able to revert the deleted section",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.sectionservice.deleteSection(id).subscribe(
            (data: any) => {
              swal.fire("Deleted!", "Section has been deleted.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
